import { SingleModelResponse } from "./SingleModelResponse";
import { ProgramCheckViewModel } from "../ViewModel/ProgramCheckViewModel";

export class ProgramCheckResponse extends SingleModelResponse<
  ProgramCheckViewModel
> {
  Model: ProgramCheckViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new ProgramCheckViewModel();
      this.Model.program_no = obj.Model.program_no;
      this.Model.program_func_desc =
        (obj && obj.Model.program_func_desc) || null;
      this.Model.temp_schema = obj.Model.temp_schema;
      this.Model.temp_table = obj.Model.temp_table;
      this.Model.temp_column_check = obj.Model.temp_column_check;
      this.Model.where_condition = (obj && obj.Model.where_condition) || null;
      this.Model.check_schema = obj.Model.check_schema;
      this.Model.check_table = obj.Model.check_table;
      this.Model.check_column = (obj && obj.Model.check_column) || null;
      this.Model.err_key = obj.Model.err_key;
      this.Model.prog_type_key = obj.Model.prog_type_key;
      this.Model.is_active = obj.Model.is_active;
      this.Model.create_time = obj.Model.create_time;
      this.Model.creator = obj.Model.creator;
      this.Model.last_update_time = obj.Model.last_update_time;
      this.Model.modifier = obj.Model.modifier;

      this.Model.is_active_desc = obj.Model.is_active_desc;
      this.Model.bus_type = obj.Model.bus_type;
      this.Model.prog_type = obj.Model.prog_type;
      this.Model.err_desc = obj.Model.err_desc;
      this.Model.program_type_seq=obj.Model.program_type_seq;
      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
          this.Model.is_active_color = "active";
      }
    }
  }
}



