import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileImportBeforeViewModel } from "../../Model/ViewModel/FileImportBeforeViewModel";
import { GlobalComponent } from "../../components/global/global";
import { ProgramCheckServicesProvider } from "../../providers/program-check-services/program-check-services";

/**
 * Generated class for the FileImportBeforeModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-import-before-check-add-edit-modal",
  templateUrl: "file-import-before-check-add-edit-modal.html"
})
export class FileImportBeforeCheckAddEditModalPage {
  item: FileImportBeforeViewModel;
  mode: string;
  CanEditBatch: boolean;
  title: string;
  FileImportBefore_type: string;
  change_mode:string;
  file_type_key:string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,
    private global: GlobalComponent,
    public ProgramCheckServices: ProgramCheckServicesProvider,
  ) {

    this.mode = navParams.data.mode;
    this.change_mode=navParams.data.change_mode;

    this.item = new FileImportBeforeViewModel();
    this.item.exec_file_seq=navParams.data.item.exec_file_seq;
    this.item.exec_seq=navParams.data.item.exec_seq;
    this.item.import_sql_seq=navParams.data.item.import_sql_seq;
    if(this.mode==="POST")
    {
      this.item.exec_seq=navParams.data.totalRows+1;

      this.item.import_sql_seq=this.item.exec_file_seq*100+this.item.exec_seq;
    }
    this.item.schemaname=navParams.data.item.schemaname;
    this.item.tablename=navParams.data.item.tablename;
    this.item.db_conn_id=navParams.data.item.db_conn_id;
    this.item.parameter_column_group=navParams.data.item.parameter_column_group;
    this.item.is_active=navParams.data.item.is_active;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;

    //this.item.sql_statement=navParams.data.item.sql_statement;
    this.item.prog_type_key=navParams.data.item.prog_type_key;
    this.item.prog_type=navParams.data.item.prog_type;
    this.item.program_no=navParams.data.item.program_no;
    this.item.program_desc=navParams.data.item.program_desc;
   // this.item.sql_statement=navParams.data.item.sql_statement;
   if(this.change_mode==="e" || this.change_mode==="c")
       this.openNavSysSqlPage(this.item);
    this.CanEditBatch = navParams.data.CanEditBatch;

    this.title=String.Format("{0}.{1}", this.item.schemaname,this.item.tablename);
  }
  openNavSysSqlPage(item) {
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {
      this.ProgramCheckServices.GetSysSqlAsync(
        item.program_no,
        item.schemaname,
        item.tablename,
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.sql_statement=data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  SelectProgram() {
    let modal = this.modalCtrl.create("BatchProgramCheckSelectModalPage", {
      select_item: this.item.program_no,
      temp_schema:this.item.schemaname,
      temp_table:this.item.tablename,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
       this.item.program_no= select_data.program_no;
       this.item.program_desc=select_data.program_func_desc;
       this.item.prog_type_key=select_data.prog_type_key;
       this.item.prog_type=select_data.prog_type;
      // this.item.schemaname=select_data.temp_schema;
      // this.item.tablename=select_data.temp_table;
       this.openNavSysSqlPage(this.item);
       //this.item.sql_statement=select_data.sql_statement;
       this.item.is_active=select_data.is_active;
    });

    modal.present();
  }
  // SelectProgType() {
  //   let modal = this.modalCtrl.create("CodeSelectModalPage", {
  //     select_key: this.item.prog_type_key,
  //     code_type: "066"
  //   });
  //   modal.onDidDismiss(select_data => {
  //     if (select_data == null) return;
  //     this.item.prog_type_key = select_data.code_key;
  //     this.item.prog_type = select_data.code_desc;
  //   });

  //   modal.present();
  // }






  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileImportBeforeModalPage");
  }
}



