import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { ProgramCheckListResponse } from "../../Model/Response/ProgramCheckListResponse";
import { ProgramCheckResponse } from "../../Model/Response/ProgramCheckResponse";
import { ProgramCheckViewModel } from '../../Model/ViewModel/ProgramCheckViewModel';
import { StringResponse } from "../../Model/Response/StringResponse";

/*
  Generated class for the ProgramCheckServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ProgramCheckServicesProvider {
  private ProgramChecks: ProgramCheckListResponse;
  private ProgramCheck: ProgramCheckResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "ProgramCheck";
    console.log("Hello ProgramCheckServiceProvider Provider");
  }
  // public GetListsAsync(PageSize: number, PageNumber: Number, keyword: string,order_type:boolean) {
  //   var sub_url: string;
  //    sub_url = "ProgramCheck?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
  //   sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
  //   return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
  //     this.ProgramChecks = new ProgramCheckListResponse({
  //       Message: item.Message,
  //       DidError: item.DidError,
  //       ErrorMessage: item.ErrorMessage,
  //       PageSize: item.PageSize,
  //       PageNumber: item.PageNumber,
  //       TotalRows: item.TotalRows,
  //       PageRows: item.PageRows,
  //       TotalPages: item.TotalPages,
  //       Model: item.Model
  //     });
  //     return this.ProgramChecks;
  //   });
  // }
  public GetListsAsync(PageSize: number, PageNumber: number, temp_schema:string,temp_table:string, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "ProgramCheck?PageSize={1}&PageNumber={2}&temp_schema={3}&temp_table={4}&keyword={5}&order_type={6}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber,temp_schema,temp_table, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.ProgramChecks = new ProgramCheckListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.ProgramChecks;
    });
  }

  public GetAsync(program_no: string) {
    var sub_url: string;
     sub_url = "ProgramCheck/{1}";
    sub_url = String.Format(sub_url, this.ctl,program_no);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.ProgramCheck = new ProgramCheckResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.ProgramCheck;
    });
  }

  public PostPutAsync(ProgramCheckViewModel:ProgramCheckViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "ProgramCheck";
    else
      sub_url = "ProgramCheck/" + ProgramCheckViewModel.program_no ;
    return this.Services.PostPutAsync(sub_url, this.ctl, true,ProgramCheckViewModel,mode).map(
      item => {
        this.ProgramCheck = new ProgramCheckResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.ProgramCheck;
      }
    );
  }

  public DeleteAsync(program_no:string) {
    var sub_url: string;
    sub_url = "ProgramCheck/"+program_no;
    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.ProgramCheck = new ProgramCheckResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.ProgramCheck;
      }
    );
  }
  public GetSqlAsync(program_no:string,method:string) {
    var sub_url: string;
    sub_url = "ProgramCheck/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,program_no,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
  public GetSysSqlAsync(program_no:string,temp_schema:string,temp_table:string) {
    var sub_url: string;
    sub_url = "ProgramCheck/get_program_check_sql?program_no={1}&schemaname={2}&tablename={3}";
    sub_url = String.Format(sub_url, this.ctl,program_no,temp_schema,temp_table);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


}



