/// <summary>
///  暫存資料表
/// <summary>
public temp_table: string;
/// <summary>
///  暫存資料表檢核欄位
/// <summary>
public temp_column_check: string;
/// <summary>
///  查詢資料條件
/// <summary>
public where_condition: string;
/// <summary>
///  檢核檔結構描述
/// <summary>
public check_schema: string;
/// <summary>
///  檢核資料表
/// <summary>
public check_table: string;
/// <summary>
///  檢核資料表欄位
